import Vue from 'vue'
import VueRouter from 'vue-router'
import MoonMiner from '../views/MoonMiner.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'MoonMiner',
    component: MoonMiner
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
